<template>
  <div class="home">
    <el-row display="margin-top:10px">
      <!-- <el-input v-model="input" placeholder="請輸入書名" style="display:inline-table; width: 30%; float:left"></el-input> -->
      <!-- <el-button type="primary" @click="addBook()" style="float:left; margin: 2px;">新增</el-button> -->
    </el-row>
    <el-row>
      <el-input v-model="input" placeholder="請輸入股票代號" style="display:inline-table; width: 30%; float:left"></el-input>
      {{ predict_result }}
      <!-- <el-table :data="bookList" style="width: 100%" border>
        <el-table-column prop="id" label="編號" min-width="100">
          <template scope="scope"> {{ scope.row.pk }} </template>
        </el-table-column>
        <el-table-column prop="book_name" label="書名" min-width="100">
          <template scope="scope"> {{ scope.row.fields.book_name }} </template>
        </el-table-column>
        <el-table-column prop="add_time" label="新增時間" min-width="100">
          <template scope="scope"> {{ scope.row.fields.add_time }} </template>
        </el-table-column>
      </el-table> -->
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'StockPredict',
  data () {
    return {
      stockCode: [],
      input: '',
      predict_result: []
    }
  },
  mounted: function () {
    window.vw = this
    this.stockCode = "['9911', '2382', '2884', '2886', '2330', '4104', '9939', '3029', '2471', '2453', '5439']"
    this.stock_predict()
  },
  methods: {
    stock_predict () {
      this.$http.get('http://127.0.0.1:8000/test2023/stock_predict?stockCode=' + JSON.stringify(this.stockCode))
        .then((response) => {
          var res = JSON.parse(response.bodyText)
          if (res.error_num === 0) {
            console.log('ok666')
            console.log('res==' + JSON.stringify(res))
            this.predict_result = JSON.stringify(res)
          } else {
            this.$message.error('預測股票methods失敗')
            console.log(res['msg'])
          }
        })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



