<template>
  <div class="hello">
    <h2>Ecosystem</h2>
    <h2>{{ msg }}</h2>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: ''
    }
  },
  mounted: function () {
    this.transParam()
  },
  methods: {
    transParam () {
      this.$http.get('http://127.0.0.1:8000/test2023/index?param=1234')
        .then((response) => {
          var res = JSON.parse(response.bodyText)
          console.log(res)
          if (res.error_num === 0) {
            this.msg = res
          } else {
            this.$message.error('失敗囉')
            console.log(res['msg'])
          }
        })
    }
  }
}
</script>



